import React from 'react'
import Section from '../components/Section';
import Triangle from '../components/Triangle';
import SEO from '../components/seo'

const Background = () => (
  <div>
    <Triangle
      color="secondaryLight"
      height={['80vh', '80vh']}
      width={['100vw', '100vw']}
      invertX
    />

    <Triangle
      color="background"
      height={['50vh', '20vh']}
      width={['50vw', '50vw']}
      invertX
    />

    <Triangle
      color="primaryDark"
      height={['25vh', '40vh']}
      width={['75vw', '60vw']}
      invertX
      invertY
    />

    <Triangle
      color="backgroundDark"
      height={['25vh', '20vh']}
      width={['100vw', '100vw']}
      invertY
    />
  </div>
);

const Portfolio = () => {
  return (
    <Section.Container id="portfolio" Background={Background}>
    <Section.Header name="Portfolio" icon="💻" label="Portfolio" />
    <SEO title="Portfolio" keywords={[`portfolio`, `Rahul Bussa Portfolio`, `react`]} />
     <article id="" className="wrapper style3">
				<div className="container">
					<header>
						<h2>Web Developer Portfolio</h2>
						<p>From Web Components to ReactJs, Angular, Node and Salesforce Lightning</p>
					</header>


					<div className="row">
						<div className="col-4 col-6-medium col-12-small">
							<article className="box style2">
								<a href="/" className="image featured"><img src="../assets/images/pic01.jpg" alt="" /></a>
								<h3><a href="/">Magna feugiat</a></h3>
								<p>Ornare nulla proin odio consequat.</p>
							</article>
						</div>
						<div className="col-4 col-6-medium col-12-small">
							<article className="box style2">
								<a href="/" className="image featured"><img src="../assets/images/pic02.jpg" alt="" /></a>
								<h3><a href="/">Veroeros primis</a></h3>
								<p>Ornare nulla proin odio consequat.</p>
							</article>
						</div>
						<div className="col-4 col-6-medium col-12-small">
							<article className="box style2">
								<a href="/" className="image featured"><img src="../assets/images/pic03.jpg" alt="" /></a>
								<h3><a href="/">Lorem ipsum</a></h3>
								<p>Ornare nulla proin odio consequat.</p>
							</article>
						</div>
					</div>
					<footer>
						<p>Lorem ipsum dolor sit sapien vestibulum ipsum primis?</p>
						<a href="#contact" className="button large scrolly">Get in touch with me</a>
					</footer>
				</div>
			</article> 
			</Section.Container>
  )
}

export default Portfolio;
